import React from "react";

import { Container,Typography } from "@material-ui/core";
import TodoList from "./todoList";
import CssTextField from './todoInput'


class App extends React.Component {
  constructor(props) {
    super();
    this.state = {
      todo: "",
      todoType: 0,
      shopTodoList: [] || "",
      shopCheckList: [] || "",
    };

    this.handleClick = this.handleClick.bind(this);
    this.handleInput = this.handleInput.bind(this);
    this.deleteItem = this.deleteItem.bind(this);
    this.handleCheck = this.handleCheck.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleKeyDown = this.handleKeyDown.bind(this);
  }
  handleInput(e) {
    this.setState({
      todo: e && e.target.value,
    });
  }
  handleChange(e) {
    this.setState({
      todoType: e && e.target.value,
    });
  }
  handleKeyDown(e) {
    if (e.key === "Enter") {
      this.handleClick();
    }
  }
  handleClick() {
    let todoList = null;
    let checkList = null;
    todoList = this.state.shopTodoList.slice();
    checkList = this.state.shopCheckList.slice();
    todoList[todoList.length] = this.state.todo;
    checkList[checkList.length] = false;
    this.setState({
      shopTodoList: todoList,
      todo: "",
      shopCheckList: checkList,
    });
  }
  handleCheck(todo) {
    let todoList = null;
    let checkList = null;
    todoList = this.state.shopTodoList.slice();
    checkList = this.state.shopCheckList.slice();
    let index = todoList.indexOf(todo);
    checkList[index] = !checkList[index];
    this.setState({
      shopCheckList: checkList,
    });
  }
  deleteItem(todo) {
    let todoList = null;
    let checkList = null;
    todoList = this.state.shopTodoList.slice();
    checkList = this.state.shopCheckList.slice();
    let index = todoList.indexOf(todo);
    todoList.splice(index, 1);
    checkList.splice(index, 1);
    this.setState({
      shopTodoList: todoList,
      shopCheckList: checkList,
    });
  }
  
  render() {

    return (
      <Container
        maxWidth="sm"
        direction="column"
        alignItems="center"
        justify="center"
        
      >
        <Typography variant="h1"   align="center" style={{color:'rgba(175, 47, 47, 0.75)'}}>
        todos
      </Typography>
        <CssTextField
          value={this.state.todo}
          onChange={this.handleInput}
          onKeyDown={this.handleKeyDown}          
        />
        <TodoList
          todoList={this.state.shopTodoList}
          checkList={this.state.shopCheckList}
          deleteItem={this.deleteItem}
          handleCheck={this.handleCheck}
        />
      </Container>
    );
  }
}
export default App;
