import React from "react";
import { ListItem, ListItemSecondaryAction } from "@material-ui/core";
import IconButton from "@material-ui/core/IconButton";
import DeleteIcon from "@material-ui/icons/Delete";
import Checkbox from "@material-ui/core/Checkbox";
import { List,ListItemText } from "@material-ui/core";


class TodoList extends React.Component {
  render() {
    const todoList = this.props.todoList;
    const checkList = this.props.checkList;

    const todos = todoList.map((todo, index) => {
      return (
            <ListItem key={index} button> 
           <IconButton>
            <Checkbox
              edge="start"
              checked={checkList[index]}
              onClick={() => this.props.handleCheck(todo)}
              style={{ color: "rgba(175, 47, 47, 0.75)" }}
            />
          </IconButton>
          <ListItemText id="standard-full-width" fullWidth >{todo}</ListItemText>
          <ListItemSecondaryAction>
            <IconButton
              edge="end"
              aria-label="delete"
              onClick={() => this.props.deleteItem(todo)}
              disabled={checkList[index]}
            >
              <DeleteIcon />
            </IconButton>
          </ListItemSecondaryAction> 
         </ListItem>         
      );
    });

    return <List>{todos}</List>;
  }
}

export default TodoList;
